import { FreeMode } from "swiper/modules";
import { Swiper, SwiperSlide } from "swiper/react";
import { useSwiper } from "swiper/react";
import DropDown from "./dropDown";
import TeletypeCarousel from "./teletypeCarousel";
import { useState } from "react";
import { ReactAudioPlayer } from "react-audio-player";
import { useEffect } from "react";
import tutorial from "./../images/tutorial.MP4";
import "swiper/css";
import "swiper/css/free-mode";

function ProductCarousel() {
  const swiperIns = useSwiper();
  const [carouselInfo, setCarouselInfo] = useState([
    "название",
    "цена",
    ["url", "url"],
  ]);
  let swiper = null;
  const [variants, setVariants] = useState("");
  const [info, setInfo] = useState("информация о продукте");
  // const [swiper, setSwiper] = useState(null);
  const [carousel, setCarousel] = useState("");
  const [slider, setSlider] = useState("");
  const [price, setPrice] = useState(0);
  const [currency, setCurrency] = useState("RUB");
  const [story, setStory] = useState(null);
  const [audio, setAudio] = useState("");
  const [choose, setChoose] = useState(["variant", "variant", "variant"]);
  function swipe(ind) {
    swiper.slideTo(ind);
  }
  useEffect(() => {
    fetch("https://pop.applepodsblack.ru/api/products?populate=deep")
      .then((response) => response.json())
      .then(function (commits) {
        let data = commits.data;
        let buffer = [];
        let urls = [];

        buffer[0] = data[window.GlobalProductId - 1].attributes.name;
        buffer[1] = data[window.GlobalProductId - 1].attributes.rub_price;
        urls[0] =
          "https://pop.applepodsblack.ru/" +
          data[window.GlobalProductId - 1].attributes.main_photo.data.attributes
            .url;
        let colors = [];
        for (let color of data[window.GlobalProductId - 1].attributes.colors
          .data) {
          urls.push(
            "https://pop.applepodsblack.ru/" +
              color.attributes.photo.data[0].attributes.url
          );
          colors.push(color.attributes.name);
        }
        buffer[2] = urls;
        buffer[3] = data[window.GlobalProductId - 1].attributes.eur_price;
        buffer[4] = data[window.GlobalProductId - 1].attributes.byn_price;
        let carousel = [];
        let vars = [];
        window.GlobalProductCategory =
          data[window.GlobalProductId - 1].attributes.category;
        for (let i = 0; i < urls.length; i++) {
          carousel.push(
            <SwiperSlide>
              <img src={urls[i]} className="product_img_carousel"></img>
            </SwiperSlide>
          );
        }
        for (let i = 1; i < urls.length; i++) {
          vars.push(
            <img
              className={choose[i]}
              src={urls[i]}
              onClick={() => {
                swipe(i);
                window.GlobalProductColor = colors[i - 1];
              }}
            ></img>
          );
        }
        if (
          data[window.GlobalProductId - 1].attributes.category == "headphones"
        ) {
          let audio_buff = [];
          let stories_buff = [];
          let counter = 0;
          audio_buff.push(
            <div className="audio_div">
              <audio
                controls
                src={
                  "https://pop.applepodsblack.ru/" +
                  data[window.GlobalProductId - 1].attributes.audio.data[1]
                    .attributes.url
                }
              ></audio>
            </div>
          );
          for (let story of data[window.GlobalProductId - 1].attributes
            .information.data) {
            stories_buff.push(
              <SwiperSlide>
                <div
                  className="story_block"
                  onClick={() => {
                    setStory(
                      // <p onClick={() => setStory(null)}>закрыть</p>
                      <video
                        onClick={() => setStory(null)}
                        src={
                          "https://pop.applepodsblack.ru/" +
                          story.attributes.url
                        }
                        autoPlay
                      ></video>
                    );
                  }}
                >
                  <div className="story_products">
                    <p>{story.attributes.name}</p>
                  </div>
                </div>
              </SwiperSlide>
            );
          }
          setAudio(audio_buff);
          setSlider(stories_buff);
        }

        setVariants(vars);

        setCarousel(carousel);
        console.log(urls.length);
        setCarouselInfo(buffer);
        setPrice(buffer[1]);
        setInfo(data[window.GlobalProductId - 1].attributes.info);
      });
  }, []);

  return (
    <div>
      <div>
        <Swiper
          onSwiper={(s) => {
            console.log(s);
            swiper = s;
          }}
          slidesPerView={1}
          modules={[FreeMode]}
          freeMode={true}
        >
          {carousel}
        </Swiper>
      </div>
      <div id="main_product_info">
        <p id="main_info_product_name">{carouselInfo[0]}</p>
        <div className="select_currency">
          <p>Выберите валюту</p>
        </div>
        <div id="main_info_product_price">
          <p id="gold_price">{price}</p>
          <div id="main_info_currencylogo">
            <select
              id="currency_choose"
              onChange={(event) => {
                if (event.target.value == "rub") setPrice(carouselInfo[1]);
                else if (event.target.value == "eur") setPrice(carouselInfo[3]);
                else setPrice(carouselInfo[4]);
              }}
            >
              <option value="rub">RUB</option>
              <option value="eur">EUR</option>
              <option value="byn">Br</option>
            </select>
          </div>
        </div>
        <div id="choose_color">
          <p>Цвет корпуса</p>
          <div className="color_variants">{variants}</div>
        </div>
        {story}
        <div id="info_dropdown">
          <DropDown content={info} header={"Описание"}></DropDown>
        </div>
        {window.GlobalProductCategory == "accessories" ||
        window.GlobalProductCategory == "dyson" ? (
          <div></div>
        ) : window.GlobalProductCategory == "watch" ? (
          <div id="stories">
            <p>Полезная информация</p>
            <TeletypeCarousel />
          </div>
        ) : (
          <div id="stories">
            <p>Полезная информация</p>
            <Swiper slidesPerView={3}>{slider}</Swiper>
          </div>
        )}
        <div id="audio_ex">{audio}</div>
      </div>
    </div>
  );
}
export default ProductCarousel;
